.tooltip {
    position: absolute;
    z-index: 1070;
    display: block;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 12px;
    font-weight: normal;
    line-height: 1.4;
    visibility: visible;
    filter: alpha(opacity=0);
    opacity: 0
}

.tooltip.in {
    filter: alpha(opacity=90);
    opacity: .9
}

.tooltip.top {
    padding: 5px 0;
    margin-top: -3px
}

.tooltip.right {
    padding: 0 5px;
    margin-left: 3px
}

.tooltip.bottom {
    padding: 5px 0;
    margin-top: 3px
}

.tooltip.left {
    padding: 0 5px;
    margin-left: -3px
}

.tooltip-inner {
    max-width: 200px;
    padding: 3px 8px;
    color: #fff;
    text-align: center;
    text-decoration: none;
    text-transform: capitalize;
    background-color: #000;
    border-radius: 4px
}

.pulse2 {
    animation-name: pulse2;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-timing-function: linear
}

@keyframes pulse2 {
    0% {
        transform: scale(1.1)
    }

    50% {
        transform: scale(0.8)
    }

    100% {
        transform: scale(1)
    }
}

.floating-arrow {
    animation-name: floating-arrow;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out
}

@keyframes floating-arrow {
    from {
        transform: translate(-50%, 0)
    }

    65% {
        transform: translate(-50%, 15px)
    }

    to {
        transform: translate(-50%, 0)
    }
}

.floating-logo {
    animation-name: floating-logo;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out
}

@keyframes floating-logo {
    from {
        transform: translate(-50%, 0)
    }

    50% {
        transform: translate(-50%, 10px)
    }

    to {
        transform: translate(-50%, 0)
    }
}

